<template>
	<div>
		<div class="banner" @click="showGalleryFun" v-lazy-container="{ selector: 'img' }">
			<img data-src="@/assets/img/15041534177378.png" alt="">
		</div>
		<fade>
		<gallary :imgs="imgArr" v-show="showGallery" @close="showGalleryFun"></gallary>
		</fade>
		<notice></notice>
		
		<detail-list :list="classify"></detail-list>
		<myfooter></myfooter>
	</div>
</template>
<script type="text/javascript">
	import footer from "common/footer"
	import gallary from "common/gallary"
	import fade from "common/fade"
	import "@/assets/css/animate.css"
	import detailList from "./detailList"
	import notice from "common/notice"
	import axios from "axios";


	export default{
		name:"detail",
		data (){
			return {
				showGallery:false,
				imgArr:[require("@/assets/img/timg.jpg"),"http://files.xubei.com/demon/8f031ace0d434f219dd7c9f0095669e8.png"],
				classify:[
					{"title":"影视专区",
					 children:[{
					 	"title":"爱奇艺vip"
					 },
				 	{
				 		"title":"优酷会员"
				 		 }
					 ]
					},
					{"title":"QQ专区",
					 children:[{
					 	"title":"QQ名片赞"
					 },{
					 	"title":"QQ超级会员 SVIP"
					 	},{
					 	"title":"QQ空间黄钻"
					 	},{
					 	"title":"QQ空间绿钻"
					 	}]
					},
					{"title":"营销推广",
					 children:[{
			 			"title":"火山小视频粉丝数购买"
					 }]
					}
				]
			}
		},
		components:{
			myfooter:footer,
			gallary,
			detailList,
			fade,
			notice
		},
		methods:{
			showGalleryFun (){
				this.showGallery=!this.showGallery;
			}
		}
	}
</script>
<style type="text/css" scoped>
.banner{
	position: relative;
	height: 0;
	padding-bottom: 46%;
	width: 100%;
}
.banner img{
	width:100%;
	display: block;
}
.bb{
	height: 400px;
}

</style>


