<template>
<div class="notice">
	<div class="scroll_wrap" ref="scroll_wrap">
		<ul>
			<template v-if="notice.length" >
			<li v-for="(item,index) of notice" ref="li">
				<a :style="{color:item.color,fontWeight: (item.bold==1?'bold':'')}">
					{{item.content}}
				</a>
			</li>
			</template>
			<li v-else>暂无公告信息</li>
		</ul>
	</div>
</div>
</template>
<script>
export default {
	data (){
		return {
			notice:[
				{"content":"新上架无限流量卡新上架无限流量卡新上架无限流量卡","color":"red",bold:1},
				{"content":"影视会员包月仅需12元","color":"green",bold:1}
			]
		}
	},
	mounted:function() {
		
		var oMarquee = document.querySelector('.scroll_wrap'); //滚动对象 
		var h=this.$refs['li'][0].clientHeight;
		var iLineHeight =h; //单行高度，像素 
		var iLineCount =this.$refs['li'].length; //实际行数 
		var iScrollAmount = 1; //每次滚动高度，像素 
		function run() { 
			oMarquee.scrollTop += iScrollAmount; 
			if ( oMarquee.scrollTop == iLineCount * iLineHeight ) 
				 oMarquee.scrollTop = 0; 
			if ( oMarquee.scrollTop % iLineHeight == 0 ) { 
				 window.setTimeout(run, 6000 ); 
			} else { 
				requestAnimationFrame(run)
			} 
			} 
		oMarquee.innerHTML += oMarquee.innerHTML; 
		setTimeout(run, 6000 ); 
	}
}

</script>
<style scoped>
	/*公告滚动*/
	 .notice{
	     background:url(../assets/img/ling.png) #ffffff no-repeat 0.1rem center;
	     background-size: 0.18rem;
	     height:0.4rem;
	     overflow: hidden;
	     padding-left: 0.32rem;
	     width: 100%;
	     box-sizing: border-box;
	     margin-bottom: 0.05rem;
	}
	 .scroll_wrap{
	     height: 0.4rem;
	     overflow: hidden;
	}
	 .scroll_wrap a{
	     white-space: nowrap;
	     text-overflow: ellipsis;
	     overflow: hidden;
	     line-height: 0.4rem;
	     height: 0.4rem;
	     display: block;
	     padding-right: 0.1rem;
	}
</style>


